@media (max-width:750px){

.logo {
  margin-top: 30px;
}

.btn {
  margin-top: 20px;
}

.description {
  min-width: 100%;
  padding-top: 30px;
  margin-top: 0px;
  text-align: center;
}

.right--align {
  text-align: center;
}

.imprint, .contact {
  margin-top: 20px;
  margin-left: 0;
}

.imprint {
  margin-right: 10px;
}

.contact {
  margin-left: 10px;
}

.screen {
  margin-top: 20px;
}

}



@media (min-width: 751px) and (max-width: 970px) {
  .description {
    display: block;
    text-align: center;
    margin-top: 30px;
  }

  .imprint, .contact {
    display: inline-block;
  }

  .right--align {
    text-align: center;
    padding-top: 20px;
  }
}

@media (min-width: 970px) and (max-width: 1170px) {
 .break {
   display: block;
 }
}
